import React, { useState, useEffect } from 'react'
import styles from './styles.module.css'

import { Source } from './Source'
import Loading from './Loading'
import { Target } from './Target'

export const Flow = ({
  name,
  description,
  flow,
  tenant,
  setOpening,
  hotglue,
  styleOverrides,
  isLinked,
  type,
  targets,
  taps
}) => {
  const [supportedSources, setSupportedSources] = useState()
  const [supportedTargets, setSupportedTargets] = useState()

  const [linkedSources, setLinkedSources] = useState()
  const [linkedTargets, setLinkedTargets] = useState()

  const [sources, setSources] = useState()

  useEffect(() => {
    ;(async () => {
      if (!type) {
        setLinkedSources(await hotglue.getLinkedSources(flow, tenant))
        setSupportedSources(await hotglue.getSupportedSources(flow))
      } else {
        setLinkedTargets(await hotglue.getLinkedTargets(flow, tenant))
        setSupportedTargets(await hotglue.getSupportedTargets(flow))
      }
    })()
  }, [])

  if (isLinked) {
    //Linked
    useEffect(() => {
      if (linkedSources && !type) {
        const _sources = linkedSources.map(({ icon, tap, label }) => {
          return (
            <Source
              key={tap}
              linked={isLinked}
              icon={icon}
              label={label}
              tap={tap}
              tenant={tenant}
              flow={flow}
              setOpening={setOpening}
              styleOverrides={styleOverrides}
            />
          )
        })

        setSources(_sources)
      } else if (linkedTargets && type) {
        const _sources = linkedTargets.map(({ icon, target, label }) => {
          return (
            <Target
              key={target}
              linked={isLinked}
              icon={icon}
              label={label}
              target={target}
              tenant={tenant}
              flow={flow}
              setOpening={setOpening}
              styleOverrides={styleOverrides}
            />
          )
        })

        setSources(_sources)

      }
    }, [linkedSources, linkedTargets])

  } else {

    //Non-Linked
    useEffect(() => {
      if (supportedSources && !type) { 
        
        const _sources = supportedSources.map(({ icon, tap, label }) => (
          <Source
            icon={icon}
            label={label}
            key={tap}
            tap={tap}
            tenant={tenant}
            flow={flow}
            setOpening={setOpening}
            styleOverrides={styleOverrides}
          />
        ))

        setSources(_sources)

      } else if (supportedTargets && type) {
        
        const _sources = supportedTargets.map(({ icon, target, label }) => (
          <Target
            icon={icon}
            label={label}
            key={target}
            target={target}
            tenant={tenant}
            flow={flow}
            setOpening={setOpening}
            styleOverrides={styleOverrides}
          />
        ))

        setSources(_sources)
      }
    }, [supportedSources, supportedTargets])
  }

  return (
    <div
      className={
        isLinked
          ? `${styles['hg-elements-flow']} ${styles['hg-elements-linkedFlow']}`
          : `${styles['hg-elements-flow']} ${styles['hg-elements-unlinkedFlow']}`
      }
      style={isLinked ? styleOverrides.linkedFlow : styleOverrides.unlinkedFlow}
    >
      <h4>{name} data</h4>
      <p>{description}</p>
      <div>{sources || <Loading />}</div>
    </div>
  )
}
